import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders, HttpParams  } from '@angular/common/http';
// import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

import { environment } from '../../../../../environments/environment';
import { config } from '../../../../shared/config';
import { LoginService } from '../../auth/login/login.service';
import {getHotelsAPI} from "../../../../shared/tenant/tenant";

@Injectable({
  providedIn: 'root'
})
export class HotelOrderService {
  constructor(private httpClient: HttpClient, private loginService: LoginService) { }

  getOrderById(orderQuery) {
    console.log(orderQuery)
    const headers = new HttpHeaders().set('access-control-allow-origin', '*')
                                       .set('api_key', 'c8b129c2e8c53de7adc8e2d1ede8d5d3');
    let params = new HttpParams();
    params = params.append('confirmationId', orderQuery);
    params = params.append('surname', orderQuery);
    params = params.append('chainCode', orderQuery);
    return this.httpClient.get( getHotelsAPI() +  'hotel/order', {params: params, headers}).pipe(
      // catchError(this.handleError)
    );
  }

  cancelOrderById(orderId: string) {
    const headers = new HttpHeaders().set('access-control-allow-origin', '*')
                                       .set('api_key', 'c8b129c2e8c53de7adc8e2d1ede8d5d3');
    let params = new HttpParams();
    // params = params.append('query', airportCode);
    return this.httpClient.delete( getHotelsAPI() +  'hotel/order' + orderId, {params: params, headers}).pipe(
      // catchError(this.handleError)
    );
  }


  getOrderByName(firstName: string, lastName: string) {
    const headers = new HttpHeaders().set('access-control-allow-origin', '*')
                                       .set('api_key', 'c8b129c2e8c53de7adc8e2d1ede8d5d3');
    let params = new HttpParams();
    params = params.append('firstName', firstName);
    params = params.append('lastName', lastName);
    return this.httpClient.get( getHotelsAPI() +  'orders/name', {params: params, headers}).pipe(
      // catchError(this.handleError)
    );
  }

  createOrder(hotelOrderQuery) {
    const headers = new HttpHeaders().set('access-control-allow-origin', '*')
                                       .set('api_key', 'c8b129c2e8c53de7adc8e2d1ede8d5d3');
    return this.httpClient.post( getHotelsAPI() +  'hotel/order', hotelOrderQuery, {headers}).pipe(
      // catchError(this.handleError)
    );
  }

  handleError(error) {
    console.log(error);
    let errorMessage = {};
    if (error.error instanceof ErrorEvent) {
      // client-side error
      errorMessage = {code:'-1', Error: `${error.error.message}`};
    } else {
      // server-side error
      errorMessage = {code:'-1', status: error.status, Error: error.message};
    }
    return throwError(errorMessage);
  }
}
